.home-page {
  height: auto;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  .container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    flex-grow: 1;
  }
}
.home-page__img {
  min-width: 1626px;
  width: 1626px;
  height: 594px;
  position: relative;
  bottom: 0;
}
.home-page__title {
  font-size: $font-size-h1;
  font-weight: $font-regular;
  color: $base-color-black;
  margin-bottom: 20px;
}
.home-page__description {
  font-size: $font-size-p-big;
  font-weight: $font-regular;
  margin-bottom: 60px;
}
.home-page__primary-btn {
  font-size: $font-size-p-big;
  font-weight: $font-bold;
  color: $base-color-bg;
  background-color: $base-color-blue;
  padding: 22px 157px;
  border-radius: 40px;
  text-decoration: none;
}
.statistic-page {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $base-color-bg;
  height: auto;
  padding-top: 100px;
  position: relative;
  z-index: 2;
  margin-top: -100px;
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
}
.statistic-page__title {
  font-size: $font-size-h3;
  font-weight: $font-regular;
  margin-bottom: 100px;
}
.statistic-page__section {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 180px;
  margin-bottom: 121px;
}
.statistic-page__section__box {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  width: 160px;
  height: 170px;
  text-align: center;
  position: relative;
  &:nth-child(1) {
    .page__section__box_img {
      height: 52px;
      width: 44px;
      position: relative;
      z-index: 4;
      transform: translateX(15%);
    }
    &::before {
      content: '';
      position: absolute;
      width: 62px;
      height: 62px;
      left: 50%;
      transform: translateX(-75%);
      border-radius: 50%;
      background-color: $box-color-pink;
      z-index: 3;
    }
  }
  &:nth-child(2) {
    .page__section__box_img {
      height: 36px;
      width: 65px;
      position: relative;
      z-index: 4;
      margin-top: 16px;
      transform: translateX(9%);
    }
    &::before {
      content: '';
      position: absolute;
      width: 62px;
      height: 62px;
      left: 50%;
      transform: translateX(-75%);
      border-radius: 50%;
      background-color: $base-color-blue;
      z-index: 3;
    }
  }
  &:nth-child(3) {
    .page__section__box_img {
      height: 48px;
      width: 52px;
      position: relative;
      z-index: 4;
      margin-top: 6px;
      transform: translateX(20%);
    }
    &::before {
      content: '';
      position: absolute;
      width: 62px;
      height: 62px;
      left: 50%;
      transform: translateX(-75%);
      border-radius: 50%;
      background-color: $box-color-green;
      z-index: 3;
    }
    .page__section__box__title {
      margin-top: 11px;
    }
  }
}
.page__section__box__title {
  font-size: $font-size-h5;
  font-weight: $font-extra-bold;
  letter-spacing: 6px;
  margin-bottom: 19px;
  margin-top: 14px;
}
.page__section__box__description {
  font-size: 17px;
  color: $base-color-gray;
}
.benefits {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 106px 0;
  height: auto;
  background-color: $base-color-bg2;
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 37px;
  }
}
.benefits__box {
  height: 370px;
  width: 1124px;
  background-color: $base-color-bg;
  border: 1px solid #E8E9ED;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 169px;
  padding: 38px;
  &:nth-child(2) {
    .hr_style {
      background-color: $base-color-blue;
    }
    gap: 192px;
    .benefits__box__title {
      width: 260px;
    }
  }
}
.right__section {
  width: 416px;
}
.benefits__box__title {
  font-size: $font-size-h3;
  margin-bottom: 30px;
}
.benefits__box__description {
  font-family: $font-family-2;
  font-size: $font-size-p-big;
  margin-bottom: 50px;
}
.hr_style {
  width: 327px;
  height: 4px;
  border: none;
  outline: none;
  background-color: $box-color-pink;
}
.left__section {
  position: relative;
  align-self: flex-end;
  &::before {
    content: '';
    position: absolute;
    width: 180px;
    height: 180px;
    border-radius: 41px;
    background-color: $box-color-pink;
    top: 0;
    right: 0;
    z-index: 1;
    transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0) translate(-50%, 20%);
  }
}
.benefits__box__img {
  position: relative;
  z-index: 2;
  margin-right: 117px;
}
.left__section2 {
  position: relative;
  &::before {
    width: 320px;
    height: 320px;
    content: '';
    position: absolute;
    border-radius: 50%;
    background-color: $base-color-blue;
    top: -5px;
    z-index: 1;
  }
}
.benefits__box__img2 {
  position: relative;
  width: 426px;
  height: 328px;
  z-index: 2;
}
.right__section2 {
  width: 416px;
}
.last__box {
  height: 370px;
  width: 1124px;
  display: flex;
  justify-content: space-between;
  gap: 60px;
}
.last__box__left {
  width: 623px;
  background-color: $base-color-bg;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 35px 70px;
  border: 1px solid #E8E9ED;
  border-radius: 5px;
  .hr_style {
    background-color: $box-color-green;
  }
  .benefits__box__title {
    width: 370px;
  }
}
.last__box__right {
  width: 440px;
  background-color: $box-color-green;
  border: 1px solid #E8E9ED;
  border-radius: 5px;
  padding: 98px 81px 64px 62px;
}
.pricing {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $base-color-bg;
  width: 100%;
  padding-top: 100px;
  margin-bottom: 155px;
  .container {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 1266px;
    height: 616px;
  }
}
.pricing__title {
  font-size: $font-size-h3;
  font-weight: $font-regular;
  margin-bottom: 50px;
}
.pricing__container__box {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 58px;
}
.pricing__box {
  width: 372px;
  height: 461px;
  border-radius: 18px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  padding-top: 34px;
  flex-direction: column;
  &:nth-child(1) {
    border: 1px solid $box-color-pink;
    .pricing__box__price {
      color: $box-color-pink;
    }
    .pricing__box__list {
      margin-top: 69px;
      li {
        &:nth-child(4) {
          &::before {
            border: 1px solid $box-color-pink;
          }
        }
        &:nth-child(5) {
          &::before {
            border: 1px solid $box-color-pink;
          }
        }
      }
    }
    li {
      &:nth-child(4) {
        &::after {
          content: '\f00d';
          color: $box-color-pink;
          transform: translateY(calc(50% + 0.25rem)) translateX(calc(-50% - 12px - 3px));
        }
      }
      &:nth-child(5) {
        &::after {
          content: '\f00d';
          color: $box-color-pink;
          transform: translateY(calc(50% + 0.25rem)) translateX(calc(-50% - 12px - 3px));
        }
      }
    }
  }
  &:nth-child(2) {
    border: 1px solid $box-color-blue;
    box-shadow: 5px 10px 27px $box-color-shadow;
    font-size: $font-size-h4;
    width: 405px;
    height: 500px;
    .pricing__box__price {
      color: $box-color-blue;
    }
    .pricing__box__list {
      margin-top: 61px;
      li {
        &:nth-child(5) {
          &::before {
            border: 1px solid $box-color-pink;
          }
        }
      }
    }
    li {
      &:nth-child(5) {
        &::after {
          content: '\f00d';
          color: $box-color-pink;
          transform: translateY(calc(50% + 0.25rem)) translateX(calc(-50% - 12px - 3px));
        }
      }
    }
    .pricing__box__link {
      top: 475px;
      background-color: $box-color-blue;
    }
  }
  &:nth-child(3) {
    border: 1px solid $box-color-green;
    .pricing__box__price {
      color: $box-color-green;
    }
    .pricing__box__list {
      margin-top: 50px;
    }
    .pricing__box__link {
      background-color: $box-color-green;
    }
  }
}
.pricing__box__title {
  font-weight: $font-bold;
  font-size: $font-size-h5;
  margin-bottom: 14px;
}
.pricing__box__price {
  font-size: $font-size-price;
  font-weight: $font-light;
}
.pricing__box__offer {
  font-weight: $font-bold;
  font-size: $font-size-p-small;
  margin-top: 5px;
}
.pricing__box__list {
  font-family: $font-family-2;
  font-size: $font-size-p-regular;
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style-type: none;
  li {
    margin-bottom: 20px;
    position: relative;
    display: inline-flex;
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%) translateX(-21px);
      width: 12px;
      height: 12px;
      border-radius: 2px;
      border: 1px solid #55DFB4;
    }
    &::after {
      font-family: "Font Awesome 5 Free", sans-serif;
      content: '\f00c';
      font-size: 0.5rem;
      font-weight: bold;
      position: absolute;
      color: $box-color-green;
      transform: translateY(calc(50% + 0.25rem)) translateX(calc(-50% - 12px - 3px));
    }
  }
}
.pricing__box__link {
  font-weight: $font-bold;
  font-size: $font-size-link-box;
  padding: 16px 57px;
  background-color: $box-color-pink;
  border-radius: 23px;
  color: $base-color-bg;
  position: absolute;
  left: 50%;
  top: 433px;
  transform: translateX(-50%);
  text-decoration: none;
}
